<template>
  <breadcrumb-list :items="breadcrumbs"></breadcrumb-list>

  <div v-if="template" class="grid grid-cols-1 gap-4 sm:gap-5 lg:gap-6">
    <div class="text-center">
      <h2 class="text-2xl">{{ template.name }}</h2>
      <p class="mt-2">{{ template.description }}</p>
    </div>

    <div
      v-for="(question, index) in template.getRenderAbleQuestions()"
      :key="index"
      class="p-4 card"
    >
      <survey-input :template="template" :question="question"></survey-input>
    </div>
  </div>
</template>

<script>
import Template from "@/models/Template";
import SurveyInput from "@/components/SurveyInputs/SurveyInput.vue";

export default {
  name: "TemplateShow",
  components: { SurveyInput },
  props: {
    id: {
      type: [String, Number],
      required: false,
    },
  },
  data() {
    return {
      breadcrumbs: [
        { title: this.$t("titles.templates"), route: "app.templates" },
        {
          title: this.$t("models.template.preview"),
          active: true,
        },
      ],
      template: new Template(),
    };
  },
  created() {
    this.template.fetch(this.id);
  },
};
</script>